import React, {useState} from "react"
// import { Link } from "gatsby"

import Layout from "../components/layout"
// import Image from "../components/image"
import SEO from "../components/seo"
import Contact from '../components/contact';

const IndexPage = () => {
  const [currentOpen, setCurrentOpen] = useState(-1)
  const handleContactClick = (index) => {
    if(currentOpen === index){
      setCurrentOpen(-1);
    }
    else{
      setCurrentOpen(index);
    }
  }
  const cts = [
    { firstName:'Clara', lastName:'Mercier', compagny:'Louis Vuitton'},
    { firstName:'Jules', lastName:'Roux', compagny:'Johnson & Johnson'},
    { firstName:'Valentina', lastName:'Petit', compagny:'Apple'},
    { firstName:'Milo', lastName:'Joly', compagny:'Johnson & Johnson'},
    { firstName:'Noa', lastName:'Petit', compagny:'Apple'},
    { firstName:'Jean', lastName:'Michel', compagny:'Johnson & Johnson'},
  ]
  return (
    <Layout
      pageTitle="contacts"
    >
      <SEO title="Home" />
      <div className='flex flex-col overflow-scroll p-3' >
        {
        cts.map((element, index) => {
          return <Contact key={element.firstName+'_'+element.lastName} contact={element} isOpen={index === currentOpen} onClick={() => handleContactClick(index)} />
        })}
      </div>
    </Layout>
  )
}

export default IndexPage
